.travelPlan-list {
  display: grid;
  grid-template-columns: 1fr 1fr 1fr;
  grid-gap: 40px;
  max-width: 95%;
  margin: 40px auto;
}
.travelPlan-list .card {
  background: #C0C0C0;
  color:#121212;
  padding: 20px;
  border-radius: 4px;
  box-shadow: 3px 3px 3px rgba(0, 0, 0, 0.05);
  transition: all 0.3s ease;
  position: relative;
}

.card-center {
  background: #C0C0C0;
  color:#FF0000;
  padding: 20px;
  padding-left: 50px;
  border-radius: 4px;
  box-shadow: 3px 3px 3px rgba(0, 0, 0, 0.05);
  transition: all 0.3s ease;
  text-align: center;
}

.travelPlan-list .card:hover {
  transform: rotate(3deg);
}
.travelPlan-list .card h3 {
  color: #121212;
  margin-bottom: 6px;
}
.travelPlan-list .card p {
  color: #121212;
  font-size: 0.9em;
}
.travelPlan-list .card ul {
  color: #121212;
  font-size: 0.9em;
}
.travelPlan-list .card h4 {
  color: #121212;
  font-size: 0.9em;
  margin-bottom: 0;
}
.travelPlan-list .card div {
  color: #121212;
  font-size: 0.7em;
  margin: 20px 0;
  line-height: 1.5em;
}

.travelPlan-list .card a {
  color: #121212;
  text-decoration: none;
  display: block;
  background: #e2e2e2;
  font-size: 0.9em;
  width: 100px;
  text-align: center;
  width: 120px;
  padding: 8px;
  border-radius: 4px;
  margin: 20px auto 0;
}

/* dark mode */
.travelPlan-list .card.dark {
  background: #202020;
}
.travelPlan-list .card.dark p,
.travelPlan-list .card.dark h3,
.travelPlan-list .card.dark h4,
.travelPlan-list .card.dark ul,
.travelPlan-list .card.dark div {
  color: #e2e2e2;
}
