@import url(https://fonts.googleapis.com/css2?family=Raleway:wght@100;200;300;400;500;600;700;800&display=swap);
/* base styles */
html, body, #root {
  height: 100%;
}
body {
  font-family: Raleway, sans-serif;
  margin: 0;
  font-size: 1.4em;
  background: #dfdfdf;
  color:#121212;
}
h1,h2,h3,p {
  margin: 0;
}
input, textarea {
  color:#121212;
  padding: 8px;
  font-size: 1em;
  border-radius: 4px;
  border: 1px solid #d4d4d4;
  background-color: #fff;
  display: block;
  width: 100%;
  box-sizing: border-box;
}
.page-title {
  text-align: center;
  margin: 40px auto;
  color:#121212;
}
.loading, .error {
  text-align: center;
  margin: 40px auto;
}

/* dark mode */
.dark .page-title,
.dark .loading,
.dark .error,
.dark label,
.dark p {
  color: #e4e4e4;
}

.navbar {
  background: #58249c;
  padding: 20px;
  color: #fff;
}

.icon{
  width:60px;
  height:60px;
  padding-right: 15px;
}
.navbar nav {
  display: flex;
  align-items: center;
  max-width: 1200px;
  margin: 0 auto;
}
.navbar a.brand {
  margin-right: auto;
  color: #fff;
  text-decoration: none;
}
.navbar a:last-child {
  color: #fff;
  text-decoration: none;
  margin-left: 40px;
  padding: 8px;
  border: 1px solid #fff;
  border-radius: 4px;
}
.navbar a:last-child:hover {
  background: #C0C0C0;
  color:#121212;
}
.Works-theme {
    color: #555;
    max-width: 90%;
    margin: 60px auto;
    background: #e3e3e3;
    color:#121212;
    padding: 20px;
    border-radius: 4px;
    box-shadow: 3px 3px 3px rgba(0, 0, 0, 0.05);
    transition: all 0.3s ease;
    position: relative;
}

/* dark mode */
.Works-theme.dark {
    background: #121212;
    color: #d4d4d4;
  }
.Home-theme {
    color: #555;
    max-width: 90%;
    margin: 60px auto;
    background: #e3e3e3;
    color:#121212;
    padding: 20px;
    border-radius: 4px;
    box-shadow: 3px 3px 3px rgba(0, 0, 0, 0.05);
    transition: all 0.3s ease;
    position: relative;
}

/* dark mode */
.Home-theme.dark {
    background: #121212;
    color: #d4d4d4;
  }
.useFetch {
    max-width: 95%;
    margin: 40px auto;
    margin-bottom: 40px;
    background: #C0C0C0;
    padding: 40px;
    box-sizing: border-box;
    color:#121212;
}

.useFetch .card {
    background: #C0C0C0;
    color:#121212;
    padding: 20px;
    border-radius: 4px;
    box-shadow: 3px 3px 3px rgba(0, 0, 0, 0.05);
    transition: all 0.3s ease;
    position: relative;
  }
 
/* dark mode */
.Create.dark {
    background: #C0C0C0;
    color: #121212;
}
.Create {
  color: #555;
  max-width: 480px;
  margin: 60px auto;
  background: #C0C0C0;
  color:#121212;
  padding: 20px;
  border-radius: 4px;
  box-shadow: 3px 3px 3px rgba(0, 0, 0, 0.05);
  transition: all 0.3s ease;
  position: relative;
}
.Create label span {
  display: block;
  margin: 30px 0 10px;
}
.Create p {
  margin-top: 10px;
  font-size: 0.8em;
}
.Create button {
  display: block;
  width: 100px;
  font-size: 1em;
  color: #fff;
  padding: 8px 20px;
  border: 0;
  border-radius: 4px;
  background-color: #58249c;
  cursor: pointer;
  text-decoration: none;
  margin: 20px auto;
}
.travelPlans {
  display: flex;
  align-items: center;
}
.travelPlans button {
  margin: 0 0 0 10px;
}

/* dark mode */
.Create.dark {
  background: #121212;
  color: #e4e4e4;
}
.travelPlan-list {
  display: grid;
  grid-template-columns: 1fr 1fr 1fr;
  grid-gap: 40px;
  max-width: 95%;
  margin: 40px auto;
}
.travelPlan-list .card {
  background: #C0C0C0;
  color:#121212;
  padding: 20px;
  border-radius: 4px;
  box-shadow: 3px 3px 3px rgba(0, 0, 0, 0.05);
  transition: all 0.3s ease;
  position: relative;
}

.card-center {
  background: #C0C0C0;
  color:#FF0000;
  padding: 20px;
  padding-left: 50px;
  border-radius: 4px;
  box-shadow: 3px 3px 3px rgba(0, 0, 0, 0.05);
  transition: all 0.3s ease;
  text-align: center;
}

.travelPlan-list .card:hover {
  -webkit-transform: rotate(3deg);
          transform: rotate(3deg);
}
.travelPlan-list .card h3 {
  color: #121212;
  margin-bottom: 6px;
}
.travelPlan-list .card p {
  color: #121212;
  font-size: 0.9em;
}
.travelPlan-list .card ul {
  color: #121212;
  font-size: 0.9em;
}
.travelPlan-list .card h4 {
  color: #121212;
  font-size: 0.9em;
  margin-bottom: 0;
}
.travelPlan-list .card div {
  color: #121212;
  font-size: 0.7em;
  margin: 20px 0;
  line-height: 1.5em;
}

.travelPlan-list .card a {
  color: #121212;
  text-decoration: none;
  display: block;
  background: #e2e2e2;
  font-size: 0.9em;
  width: 100px;
  text-align: center;
  width: 120px;
  padding: 8px;
  border-radius: 4px;
  margin: 20px auto 0;
}

/* dark mode */
.travelPlan-list .card.dark {
  background: #202020;
}
.travelPlan-list .card.dark p,
.travelPlan-list .card.dark h3,
.travelPlan-list .card.dark h4,
.travelPlan-list .card.dark ul,
.travelPlan-list .card.dark div {
  color: #e2e2e2;
}

